.table {
  inline-size: 100%;
  border-collapse: collapse;
  font-size: 0.875rem;
  white-space: nowrap;
}

  .table [align='right'] > * {
    text-align: end;
  }

  .table [align='center'] > * {
    text-align: center;
  }

  .table thead {
    text-transform: uppercase;
    color: var(--mui-palette-text-primary);
  }

  .table thead th {
      font-weight: 500;
      font-size: 0.8125rem;
      letter-spacing: 0.2px;
      line-height: 1.8462;
      text-align: start;
      block-size: 56px;
      background-color: var(--mui-palette-customColors-tableHeaderBg);
    }

  .table thead th:not(:first-of-type):not(:last-of-type) {
        padding-block: 0.5rem;
        padding-inline: 1rem;
      }

  .table thead th:first-of-type:not(:has(input[type='checkbox'])) {
          padding-block: 0.5rem;
          padding-inline: 1.25rem 1rem;
        }

  .table thead th:first-of-type:has(input[type='checkbox']) {
          padding-inline-start: 0.6875rem;
        }

  .table thead th:last-of-type {
        padding-block: 0.5rem;
        padding-inline: 1rem 1.25rem;
      }

  .table tbody {
    color: var(--mui-palette-text-secondary);
  }

  .table tbody th,
    .table tbody td {
      font-size: 0.9375rem;
      line-height: 1.4667;
      block-size: 50px;
    }

  .table tbody th:not(:first-of-type):not(:last-of-type), .table tbody td:not(:first-of-type):not(:last-of-type) {
        padding-block: 0.5rem;
        padding-inline: 1rem;
      }

  .table tbody th:first-of-type:not(:has(input[type='checkbox'])), .table tbody td:first-of-type:not(:has(input[type='checkbox'])) {
          padding-block: 0.5rem;
          padding-inline: 1.25rem 1rem;
        }

  .table tbody th:first-of-type:has(input[type='checkbox']), .table tbody td:first-of-type:has(input[type='checkbox']) {
          padding-inline-start: 0.6875rem;
        }

  .table tbody th:last-of-type, .table tbody td:last-of-type {
        padding-block: 0.5rem;
        padding-inline: 1rem 1.25rem;
      }

  .table tbody tr:not(:last-child) {
      border-block-end: 1px solid var(--border-color);
    }

.cellWithInput input {
  inline-size: 100%;
  background-color: transparent;
  font-size: inherit;
  color: inherit;
  border-radius: var(--mui-shape-customBorderRadius-sm);
  padding-block: 6px;
  padding-inline: 10px;
  margin-inline: -10px;
}

.cellWithInput input:focus-visible {
    outline: 1px solid var(--mui-palette-primary-main);
  }
